@import '../../../base';

.landing-form-outer-container {
  border-radius: 7px;
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: 0 30px 40px 0 rgba(133, 195, 255, 0.25);
  margin: auto;
  margin-bottom: 200px;
  margin-top: -60px;
  padding: 68px 40px;
  max-width: 945px;

  .desc {
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-title;
    position: absolute;
    margin-top: -150px;
    max-width: 550px;
    z-index: 1000;
    width: calc(100% - 136px);
  }

  .promo-button {
    position: absolute;
    transform: translateY(-125px);
    z-index: 1001;
    @media screen and (max-width: 576px) {
      position: absolute;
      transform: translateY(-50px);
    }

    .promo {
      transition: transform 0.3s ease;

      &:hover {
        transform: scale(1.105);
      }
    }
  }
  
  .tab-view-container {
    margin-top: 90px;
    @media screen and (min-width: 576px) {
      margin-top: 50px;
    }

    .tab-container {
      padding-bottom: 10px;
      @media screen and (min-width: 576px) {
        margin-left: 16.66666%;
        transform: translateX(-20px);
      }
    }
  
    .col-12 {
      margin: 10px 0;
    }
  }

  @media screen and (max-width: 576px) {
    padding: 0 10px;

    .desc {
      font-size: 20px;
      font-weight: $font-weight-semibold;
      margin-top: -60px;
      max-width: 250px;
      width: calc(100% - 60px);
    }
  }

  .input-wrapper-desc {
    font-weight: $font-weight-bold;
  }
}

.promo-outer-extra {
  .desc {
    margin-top: -210px;
  }

  @media screen and (max-width: 576px) {
    margin-top: -100px !important;

    .desc {
      margin-top: -110px;
    }
  }
}

.landing-form-img-container {
  display: flex;
  flex-direction: row; 
  min-height: 210px;
  overflow: hidden;
  margin-top: -30px;

  img {
    height: 70%;
  }
  .img-1 {
  }
  
  .img-2 {
    position: absolute;
    right: 0;
  }
  
  .img-3 {
    position: absolute;
    right: 25vw;
    z-index: 999;
  }
}

@media screen and (max-width: 576px) {
  .landing-form-img-container {
    display: flex;
    flex-direction: row; 
    min-height: 150px;
  
    .img-1 {
      position: absolute;
      height: 30%;
      left: -18%;
      bottom: 0;
    }
    
    .img-2 {
      position: absolute;
      right: -40%;
      height: 60%;
      bottom: 0;
    }
    
    .img-3 {
      position: absolute;
      right: 15vw;
      height: 60%;
      z-index: 999;
      bottom: 0;
    }
  }
}

.landing-form-outer-width {
  max-width: 945px;
  flex: 1;
  margin: auto;
  margin-bottom: 40px;
}

.landing-form-container {
  max-width: 1006px;
  .edit-header {
    text-align: center;
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-title;
  }

  .input-offset {
    margin-top: 28px;
  }

  .button,
  .icon {
    display: flex;
    align-self: flex-start;
  }
  .icon {
    margin-left: -20px;
  }
  .icon-car {
    margin-top: 45px;
  }

  .icon-chest {
    margin-top: 35px;
  }
  .button-container {
    margin-top: 60px;

    .button-left {
      margin-right: 23px;
    }

    @media screen and (max-width: 576px) {
      margin-top: 10px;

      .button-left {
        margin-right: 0;
        margin-bottom: 10px;
      }
    }

    .button-container-inner {
      display: flex;
      justify-content: center;
      align-items: center;

      @media screen and (max-width: 576px) {
        &:nth-of-type(2) {
          margin-bottom: 50px;
        }
      }
    }
  }

  .row-margin {
    margin-top: 10px;
  }
  .no-horizontal-margin {
    margin-left: 0;
    margin-right: 0;
  }

  .input-padding-left {
    padding-left: 13px;
  }

  .input-padding-right {
    padding-right: 13px;
  }

  .birth-date-container {
    position: relative;

    .age {
      position: absolute;
      top: 0;
      right: 0;
      margin: 0;
      transform: translateY(10px);
      
      .age-value,
      .inactive,
      .active {
        font-size: $font-size-big * 0.7;
        font-weight: $font-weight-bold;
      }
      
      .inactive {
        color: $color-line;
      }

      .active {
        color: $color-body;
      }
      
      .age-value {
        color: $color-title;
        min-width: 30px;
        margin-left: 10px;
        margin-right: 5px;
      }
    }
  }

  .slider-container {
    @media screen and (min-width: 576px) {
      max-width: calc(100% - 15px) !important;
      transform: translateX(-8px);
    }
  }
}

@media screen and (max-width: 576px) {
  .landing-form-container {
    padding: 15px;
    padding-bottom: 80px;

    .input-offset {
      margin-top: 78px;
    }

    .input-padding-left {
      padding-left: 0;
    }
  
    .input-padding-right {
      padding-right: 0;
    }
  }
}

.promo-img-extra {
  margin-top: -20px;
  @media screen and (max-width: 576px) {
    margin-top: 0;
    min-height: 220px;
  }
}